const wx = window.wx || {}
var isLogin = false
export const initWechatJs = (appId, nonceStr, timestamp, signature, router, link, imgUrl) => {
  if (isLogin === false) {
    wx.config({
      debug: false,
      appId: appId,
      timestamp: timestamp,
      nonceStr: nonceStr,
      signature: signature,
      jsApiList: [
        'checkJsApi',
        'chooseImage',
        'uploadImage',
        'onMenuShareTimeline',
        'getLocalImgData',
        'onMenuShareAppMessage'
      ]
    })
  }
  isLogin = true
  wx.ready(function () {
    // wx.showOptionMenu()
    if (router === '/sharephoto') {
      // wx.onMenuShareTimeline({
      //   title: ``,
      //   link: 'http://coach.realmshow.com?isLast=1',
      //   imgUrl: `http://citic365.e-mallchina.com.cn/image/shareImg.jpg`
      // })
      // wx.onMenuShareAppMessage({
      //   title: ``, // 
      //   desc: ``, // 
      //   link: 'http://coach.realmshow.com?isLast=1', // 
      //   imgUrl: `http://citic365.e-mallchina.com.cn/image/shareImg.jpg` // 
      // })
      shareInfo(link, imgUrl)
    } else {
      wx.onMenuShareTimeline({
        title: `coach`,
        link: 'http://coach.realmshow.com',
        imgUrl: ``
      })
      wx.onMenuShareAppMessage({
        title: `coach`, // 
        desc: `coach`, // 
        link: 'http://coach.realmshow.com', // 
        imgUrl: `` // 
      })
    }
  })
}

export const openCameraForPic = function (type, uploadCallback) {
  var sourceType = []
  if (type === 1) {
    sourceType.push('album')
  } else {
    sourceType.push('camera')
  }
  wx.chooseImage({
    count: 1, // 9
    sizeType: ['original', 'compressed'], // 
    sourceType: sourceType, // 
    success: function (res) {
      var localIds = res.localIds // IDlocalIdimgsrc
      console.log(localIds)
      if (uploadCallback && typeof uploadCallback === 'function') {
        uploadCallback(localIds)
      }
    },
    error: function (e) {
      alert(e)
    }
  })
}

export const shareInfo = function (link, imgUrl) {
  console.log(link)
  console.log(imgUrl)
  wx.onMenuShareTimeline({
    title: 'coach', // 
    link: link, // JS
    imgUrl: imgUrl, // 
    success: function () {
      console.log()
    },
    cancel: function () {
      alert('')
    }
  })
  wx.onMenuShareAppMessage({
    title: `coach`, // 
    desc: `coach`, // 
    link: link, // 
    imgUrl: imgUrl // 
  })
}



